.container {
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;
}
@media screen and (min-width: 320px) {
    .container {
        width: 320px;
        padding: 0 20px;
    }
}
@media screen and (min-width: 768px) {
    .container {
        width: 768px;
        padding: 0 40px;
    }
}
@media screen and (min-width: 1316px) {
    .container {
        width: 1286px;
        padding: 0 15px;
    }
}